.desert-wrapper {
  margin-top: 16px;
  padding: 0 16px;
}

.desert-title {
  font-size: 14px;
  font-weight: 600;
  margin-bottom: 12px;
  color: #333;
}

.highlight {
  color: #76a37c; /* 포인트 컬러 */
}

.desert-card {
  display: flex;
  align-items: center;
  background: #ffffff;
  padding: 16px;
  border-radius: 16px;

  /* ✅ 테두리 + 그림자 */
  /* border: 1.5px solid #e3f3d9; 연한 파스텔 그린 */
  box-shadow: 0 3px 8px rgba(0, 0, 0, 0.08);

  transition: box-shadow 0.2s ease, transform 0.2s ease;
}

.desert-img {
  width: 56px;
  height: 56px;
  border-radius: 50%; /* 원형 */
  background: #e3f3d9; /* 아이콘 배경도 같은 계열 */
  padding: 10px; /* 아이콘 여백 */
  object-fit: contain;
  margin-right: 14px;
  flex-shrink: 0;
}

.desert-content {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 3px;
}

.desert-name {
  font-size: 16px;
  font-weight: 600;
  color: #2c2c2c;
  margin-bottom: 4px;
}

.desert-link,
.desert-info {
  font-size: 13px;
  color: #666;
  display: inline-flex;
  align-items: center;
  gap: 4px;
  text-decoration: none;
  transition: color 0.2s ease;
}

.desert-link:hover {
  color: #008a69;
  text-decoration: underline;
}

.desert-info {
  color: #aaa; /* 일반 음식일 경우 옅은 색 */
}
