.meal-item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  /* border-top: 1px solid #ddd; */
  max-width: 90%; /* 구분선 줄이기 */
  margin: 0 auto; /* 가운데 정렬 */
  /* 아침헤더도 바꾸고 싶으면 얘랑 같게 */
  padding: 9px 1px;
}

.meal-item:first-child {
  border-top: none;
}

.item-left {
  display: flex;
  align-items: center;
  gap: 10px;
}

.food-icon {
  width: 40px;
  height: 40px;
  /* color: #4caf50; */
}
.food-name {
  font-size: 15px;
  font-weight: 700;
  color: #111;
}

.recipe-button {
  font-size: 12px;
  padding: 3px 7px;
  background-color: #ececec;
  color: #a7a7a7;
  border: none;
  border-radius: 20px;
  cursor: pointer;
}
