.banner-container {
  width: 90%;
  max-width: 600px;
  height: 180px;
  margin: 20px auto;
  padding: 20px;
  background-color: #ddf0ea; /* ✅ 밝은 민트그린 */
  border-radius: 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
  overflow: hidden;
  /* border: 1px solid #d9d9d9; */
  box-shadow: 0 2px 3px rgba(0, 0, 0, 0.15);
}

.text-button-wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  z-index: 1;
}

.main-title {
  font-size: 15px;
  font-weight: 800;
  color: #004d3d; /* ✅ 진한 초록 */
  margin: 0 0 7px 0;
  line-height: 1.4;
}

.main-text1 {
  margin-bottom: 5px;
}

.main-text2 {
  color: #008a69; /* ✅ 메인 초록 강조 */
  font-size: 16px;
}

.sub-text {
  font-size: 12px;
  color: #333; /* ✅ 가독성 있는 진회색 */
  margin: 0 0 13px 0;
  line-height: 1.4;
}

.convert-button {
  background-color: #008a69;
  color: white;
  padding: 7px 30px;
  border: none;
  border-radius: 25px;
  font-size: 14px;
  font-weight: bold;
  cursor: pointer;
  transition: transform 0.2s, background 0.2s;
}

.convert-button:hover {
  transform: translateY(-2px);
  background-color: #00694d; /* ✅ hover 시 진해짐 */
}

.banner-image {
  position: absolute;
  right: 0px;
  bottom: 0;
  height: 180px;
  width: 180px;
  object-fit: contain;
}
