/* Category Item */
.category-item-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  width: 80px;
  margin: 8px 8px;
  cursor: pointer;
}

.category-image {
  width: 60px;
  height: 60px;
  border-radius: 16px;
  border: 1px solid #e5e7eb; /* 밝은 회색 */
  background: #f9fbfb; /* SortBar랑 어울리는 은은한 배경 */
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;

  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.05);
  transition: transform 0.2s, box-shadow 0.2s, background 0.2s;
}

.category-image:hover {
  transform: translateY(-3px);
  background: #ffffff; /* Hover 시 살짝 더 밝게 */
  box-shadow: 0 6px 14px rgba(0, 138, 105, 0.15),
    0 3px 6px rgba(0, 138, 105, 0.1);
}

.category-image img {
  width: 90%;
  height: 90%;
  object-fit: contain; /* 아이콘 크기 잘 맞게 */
}

.category-name {
  margin-top: 8px;
  font-size: 13px;
  color: #333;
  font-weight: 600;
}
