.recipe-card-container {
  width: 160px; /* 캐러셀 내 아이템 너비 */
  flex-shrink: 0;
  min-height: 233px; /* ✅ 카드 높이 고정 */
  max-height: 233px;
  background-color: #fff;
  border-radius: 15px;
  overflow: hidden;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transition: transform 0.2s ease-in-out;
  cursor: pointer;
  margin-top: 4px;
}

.recipe-card-container:hover {
  transform: translateY(-3px);
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.15);
}

.recipe-image-wrapper {
  width: 100%;
  height: 150px;
  overflow: hidden;
}

.recipe-image-wrapper img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.recipe-info {
  padding: 10px;
  display: flex;
  flex-direction: column;
}

.recipe-meta {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 5px;
}

/* .recipe-tags {
  font-size: 11px;
  color: #888;
} */
.heart-icon {
  font-size: 16px;
  color: #ddd; /* 기본 하트 색상 */
  cursor: pointer; /* 클릭 가능하도록 커서 변경 */
  transition: color 0.2s; /* 색상 변경 시 부드럽게 */
}

/* 하트가 '좋아요' 상태일 때의 색상 */
.heart-icon.liked {
  color: #ff6b6b; /* 빨간색으로 변경 */
}

.recipe-name {
  font-size: 15px;
  font-weight: bold;
  color: #000000;
  margin: 0;
  text-align: left;
}

.highlight {
  color: #008a69 !important; /* 초록색 */
  font-weight: 700;
}

.recipe-meta-text {
  font-size: 12px;
  color: #666;
}

.no-image {
  width: 100%;
  height: 150px; /* .recipe-image-wrapper와 동일한 높이 */
  display: flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(135deg, #f0f0f0, #e0e0e0);
  color: #666;
  font-size: 14px;
  font-weight: 500;
  border-radius: 8px;
}
