.detail-step {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  gap: 12px;
  padding: 12px 28px;
  /* border-bottom: 1px solid #eee; */
}

.step-info {
  flex: 1;
}

.step-badge {
  display: inline-block;
  background: #76a37c;
  color: #fff;
  font-size: 12px;
  font-weight: 600;
  border-radius: 12px;
  padding: 4px 10px;
  margin-bottom: 6px;
}

.step-desc {
  font-size: 14px;
  color: #333;
  margin: 0;
  line-height: 1.4;
  max-width: 230px;
}

.step-image img {
  width: 80px;
  height: 80px;
  border-radius: 6px;
  object-fit: cover;
}

.no-step-image {
  width: 80px;
  height: 80px; /* 이미지랑 동일 크기 */
  display: flex;
  align-items: center;
  justify-content: center;
  background: #f3f4f6; /* 연한 회색 배경 */
  color: #9ca3af; /* 회색 텍스트 */
  font-size: 12px;
  font-weight: 500;
  border-radius: 6px; /* 이미지와 동일 radius */
}
