:where(.tb-screen) {
  --bg: #ffffff;
  --ink: #0f172a;
  --muted: #6b7280;
  --brand: #008a69; /* 진행바 색상 */
}

/* 전체 화면 중앙 배치 */
.tb-screen {
  min-height: 800px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: var(--bg);
  padding-top: 2px;
  text-align: center;
  gap: 28px;
}

/* 상단 작은 텍스트 */
.tb-sub {
  color: var(--muted);
  font-weight: 700;
  font-size: 16px;
}

/* 가운데 큰 타이틀 */
.tb-title {
  color: var(--ink);
  font-weight: 900;
  font-size: 25px;
  line-height: 2.35;
}

/* 일러스트 → 꽉 차게 */
.tb-illu {
  width: 100%;
  max-width: none;
  height: auto;
  display: block;
  padding-bottom: 20px;
  padding-top: 20px;
}

/* 진행 상태 */
.tb-progress-wrap {
  width: 100%;
  max-width: 320px;
  display: flex;
  flex-direction: column;
  gap: 10px;
}
.tb-percent {
  font-weight: 700;
  font-size: 14px;
}

.tb-percent .num {
  color: var(--brand);
  font-weight: 900;
}

.tb-percent .label {
  color: var(--muted); /* 기존 회색 */
  margin-left: 2px;
}

.tb-progress {
  height: 12px;
  background: #e5e7eb;
  border-radius: 999px;
  overflow: hidden;
}

.tb-bar {
  height: 100%;
  width: 0;
  background: var(--brand);
  transition: width 0.2s linear;
}
.tb-loading {
  margin-top: 20px;
  display: flex;
  justify-content: center;
}

.spinner {
  width: 40px;
  height: 40px;
  border: 4px solid #eee;
  border-top: 4px solid #6a5acd; /* 보라색 */
  border-radius: 50%;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}
